@import url("https://fonts.googleapis.com/css2?family=PT+Serif:wght@400;700&family=Poppins:wght@300;700&display=swap");
:root {
  --black: #0A0C08;
  --white: #E6FAFC;
  --grey: #c0cfd1;
  --green: #43F939;
  --flair: #43f93934;
  --purple: #27102e;
  --black-purple: #100514;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1, h4 {
  font-family: "PT Serif", serif;
  font-weight: 400;
}
h1 span, h4 span {
  font-weight: 700;
  word-break: keep-all;
}

p {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
}
p span {
  font-weight: 700;
}

html {
  height: 100%;
  scroll-behavior: smooth !important;
}
html body {
  background: var(--purple);
  min-height: 100%;
}
html body .App {
  background: radial-gradient(circle farthest-side at var(--x) var(--y), var(--purple) 0%, var(--black-purple) 100%);
  background-repeat: no-repeat;
  background-attachment: fixed;
  height: 100%;
  width: 100%;
  display: grid;
}
html body .App .bioColumn {
  width: 40%;
  height: 100%;
  position: fixed;
  padding: 100px 50px 100px 100px;
  color: var(--white);
}
html body .App .bioColumn h1 {
  font-size: 50px;
  line-height: 50px;
  margin-bottom: 20px;
}
html body .App .bioColumn h4 {
  font-size: 16px;
  color: var(--grey);
}
html body .App .bioColumn .sec-list {
  display: flex;
  flex-direction: column;
  margin: 30px 0;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  gap: 20px;
  font-size: 15px;
  transition: 0.2s ease-in-out;
}
html body .App .bioColumn .sec-list a {
  text-decoration: none;
  color: var(--grey);
  width: auto;
  padding-left: 30px;
  transition: 0.2s ease-in-out;
}
html body .App .bioColumn .sec-list a::after {
  content: "";
  background-color: var(--green);
  position: absolute;
  left: 2px;
  margin-left: 100px;
  margin-top: 0;
  width: 2px;
  height: 23px;
  transition: 0.2s ease-in-out;
}
html body .App .bioColumn .sec-list a:hover::after {
  margin-top: 10px;
  width: 60px;
  height: 2px;
}
html body .App .bioColumn .sec-list:hover a {
  opacity: 0.2;
  transition: 0.2s ease-in-out;
}
html body .App .bioColumn .sec-list:hover a:hover {
  font-weight: 700;
  opacity: 1;
  color: var(--green);
  padding-left: 80px;
}
html body .App .bioColumn .soc-list {
  display: flex;
  gap: 50px;
}
html body .App .bioColumn .soc-list a {
  height: 30px;
  width: 30px;
  color: white;
}
html body .App .bioColumn .soc-list a img {
  filter: invert(100%) brightness(75%);
  cursor: pointer;
}
html body .App .infoColumn {
  width: 60%;
  height: 100%;
  margin-left: auto;
  padding: 0 100px 100px 100px;
  color: var(--grey);
}
html body .App .infoColumn .margin {
  height: 100px;
}
html body .App .infoColumn .educ-section h1 {
  margin-bottom: 10px;
}
html body .App .infoColumn .educ-section .educCard {
  display: flex;
  gap: 20px;
  padding: 20px;
  transition: ease-in-out 0.2s;
}
html body .App .infoColumn .educ-section .educCard a {
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  text-decoration: none;
}
html body .App .infoColumn .educ-section .educCard:hover {
  border-radius: 15px;
  background: linear-gradient(145deg, #2a1131, #230e29);
  box-shadow: 5px 5px 18px rgba(16, 6, 18, 0.8039215686), -5px -5px 18px #3d1a48;
}
html body .App .infoColumn .proj-section h1 {
  margin-bottom: 10px;
}
html body .App .infoColumn .proj-section .projCard {
  display: flex;
  gap: 20px;
  padding: 20px;
  transition: ease-in-out 0.2s;
  margin: 10px 0;
}
html body .App .infoColumn .proj-section .projCard a {
  color: white;
  font-family: "Poppins", sans-serif;
  font-weight: 700;
  text-decoration: none;
}
html body .App .infoColumn .proj-section .projCard:hover {
  border-radius: 15px;
  background: linear-gradient(145deg, #2a1131, #230e29);
  box-shadow: 5px 5px 18px rgba(16, 6, 18, 0.8039215686), -5px -5px 18px #3d1a48;
}
html body .App .infoColumn .proj-section .projCard:hover .linkIcon {
  transition: 0.06s ease-in-out;
  right: -1px;
  top: -1px;
  filter: invert(100) brightness(100%);
}
html body .App .infoColumn .proj-section .projCard .thumbnail {
  border: 2px solid rgba(192, 207, 209, 0.3294117647);
  border-radius: 4px;
  width: 100px;
  height: 100px;
  -o-object-fit: cover;
     object-fit: cover;
}
html body .App .infoColumn .proj-section .projCard .linkIcon {
  height: 15px;
  width: 15px;
  filter: invert(100) brightness(70%);
  margin-left: 6px;
  position: relative;
  top: 1px;
}
html body .App .infoColumn .proj-section .projCard .flairs {
  margin-top: 5px;
  display: flex;
  gap: 10px;
}
html body .App .infoColumn .proj-section .projCard .flairs div {
  font-size: 12px;
  background-color: var(--flair);
  color: var(--green);
  padding: 2px 8px;
  border-radius: 12px;
}

@media screen and (max-width: 767px) {
  html body .App {
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
  }
  html body .App h1, html body .App h4 {
    text-align: center;
  }
  html body .App .bioColumn {
    padding: 50px;
    width: 100%;
    height: 100%;
    position: relative;
  }
  html body .App .bioColumn a::after {
    left: 2px;
    margin-left: 50px !important;
  }
  html body .App .infoColumn {
    padding: 50px;
    width: 100%;
    height: 100%;
  }
  html body .App .infoColumn .projCard {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  html body .App .infoColumn .projCard .thumbnail {
    width: 100% !important;
    min-width: 200px !important;
    height: 100%;
  }
}
.sectionVisible {
  animation: fade-in 0.5s ease-in-out;
  position: relative;
}

@keyframes fade-in {
  0% {
    color: var(--green);
    text-shadow: 4px 0px 2px var(--green);
    right: -10px;
    opacity: 0%;
  }
  100% {
    right: 0;
    opacity: 100%;
  }
}/*# sourceMappingURL=App.css.map */